import React, { useEffect, useCallback, useState} from 'react';
import FinishedUploading  from './FinishedUploading.js';
import ipfs from 'decentralized/Ipfs.js';
import Modal from 'components/modal/Modal.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import {connect} from 'react-redux';
import {getSporeRemixes} from 'spores/getSporeRemixes.js';
import './recording.scss';

const SporeRecorded = ({spTokenId, tokenId, collectionAddress, clear, }) => { 
    let [sporeRecorded, setSporeRecorded] = useState();
    let [uploadingRecording, setUploadingRecording] = useState(false);

    useEffect(() => {
        /*
        if (collectionAddress) {
            getSporeRemixes(collectionAddress, tokenId);
        }
        setTimeout(() => {
            setSporeRecorded("@mmmmm");
        }, 5000);
        */
    }, []);
    
    const complete = useCallback(() => {
        clear();
        setSporeRecorded(undefined);
        setUploadingRecording(undefined);
    }, [setSporeRecorded, setUploadingRecording]);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
        return () => window.removeEventListener("message", onMessage);
    }, [setSporeRecorded, setUploadingRecording]);

    const onMessage = useCallback(e => {
        let data = e.data;
        if (data.type === "uploadingRecording") {
            setUploadingRecording(true);
        } else if (data.type == "sporeRecorded") {
            let json = {
                sliderChanges: data.body.sliderData,
                modeChanges: data.body.modeData
            };
            ipfs.addJson(json).then(
                hash => {
                    setSporeRecorded({
                        recordingHash: data.body.recording,
                        dataHash: hash
                    });
                });
        }
    }, [setSporeRecorded, setUploadingRecording]);

    if (!sporeRecorded && !uploadingRecording) {
        return '';
    }
    return (
        <Modal hide={e => []}>
          {!sporeRecorded ?
           <div className="spore-recorded-dialog">
             <div className="spore-header">
               Spores recorded your remix.
             </div>
             <div className="uploading-text">
               Processing your new sound.
             </div>
             <div className="progress-container">
               <UnknownProgressBar width={180}/>
             </div>
           </div> : 
           <FinishedUploading
             spTokenId={spTokenId}
             tokenId={tokenId}
             complete={complete}
             collectionAddress={collectionAddress}
             sporeRecorded={sporeRecorded}/>
}
        </Modal>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeRecorded);
