import AudioUnit from 'engine/audio.js';
import React, {useCallback, useEffect, useState} from 'react';
import Tone from 'tone';
import ipfs from 'decentralized/Ipfs.js';

const bufferToArrayBuffer = require('buffer-to-arraybuffer');

export const useAudioPlayer = (hash) => {
    let [audioPlayer, setAudioPlayer] = useState();

    const dispose = useCallback(() => {
        if (audioPlayer) {
            audioPlayer.dispose();
        }
    }, [audioPlayer]);

    useEffect(() => {
        ipfs.getSound(hash, 10000000).then(
            async blob => {
                let audioBuffer = await Tone.Master.context.decodeAudioData(bufferToArrayBuffer(blob));
                let player = new AudioUnit(Tone.Master.context);
                player.setBuffer(audioBuffer);
                setAudioPlayer(player);
            });
    }, [hash, setAudioPlayer]);

    return {
        audioPlayer,
        dispose
    };
};
